body {
  height: 100vh;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab) 0 0 / 400% 400%;
  animation: gradient 15s infinite;
}

@keyframes gradient {
  0% {
    background-position: 0%;
  }

  50% {
    background-position: 100%;
  }

  100% {
    background-position: 0%;
  }
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.search-form {
  justify-content: center;
  align-items: center;
  margin: 20px;
  display: flex;
}

.search-form input[type="text"] {
  max-width: 300px;
  border: 2px solid #ccc;
  border-radius: 5px;
  flex: 1;
  margin-right: 10px;
  padding: 8px;
  font-size: 16px;
}

.search-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
}

.search-form button:hover {
  background-color: #0056b3;
}

img {
  width: 300px;
  height: 200px;
  object-fit: cover;
  display: block;
}

p {
  margin-top: 3px;
  margin-bottom: 0;
}

.info {
  text-align: center;
  vertical-align: center;
  justify-content: space-between;
  padding-left: 5px;
  padding-right: 5px;
  display: flex;
}

#button {
  width: 40px;
  height: 40px;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  z-index: 1000;
  background-color: #007bff;
  border: none;
  border-radius: 50%;
  justify-content: center;
  transition: background-color .3s, opacity .5s, visibility .5s;
  display: inline-block;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

#button:after {
  content: "ÔÅ∑";
  color: #fff;
  font-family: FontAwesome;
  font-size: 2em;
  font-style: normal;
  font-weight: normal;
  line-height: -25px;
}

#button:hover {
  cursor: pointer;
  background-color: #333;
}

#button:active {
  background-color: #555;
}

#button.show {
  opacity: 1;
  visibility: visible;
}

.gallery {
  color: #fff;
  text-shadow: .1em .1em .2em #000;
}

.photo-card {
  transition: transform .3s;
}

.photo-card:hover {
  transform: scale3d(1.05, 1.05, 1);
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

